.logoIcon {
  position: relative;
  width: 120.66px;
  height: 16.08px;
  flex-shrink: 0;
}
.left {
  align-self: stretch;
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  padding: var(--padding-sm) var(--padding-md);
  align-items: center;
  justify-content: flex-start;
}
.spacingComponentNordUi {
  position: relative;
  background-color: var(--color-green);
  width: 24px;
  height: 24px;
  flex-shrink: 0;
  opacity: 0;
}
.logOut1 {
  position: relative;
  font-size: var(--font-size-base);
  line-height: 21px;
  font-weight: 500;
  font-family: var(--font-roboto);
  color: var(--text-white);
  text-align: left;
}
.logOut {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.actionhamburger24Icon {
  position: relative;
  width: 24px;
  height: 24px;
  flex-shrink: 0;
}
.header,
.right,
.small {
  flex-direction: row;
  justify-content: flex-start;
}
.small {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  display: none;
  align-items: flex-start;
}
.header,
.right {
  display: flex;
  align-items: center;
}
.right {
  overflow: hidden;
  padding: var(--padding-sm) 0;
}
.header {
  align-self: stretch;
  background-color: var(--color-black);
  border-bottom: 1px solid var(--color-violet);
  box-sizing: border-box;
  height: 60px;
  flex-shrink: 0;
}
.container,
.text {
  align-items: center;
  justify-content: center;
}
.tableGoesHere {
  position: relative;
  line-height: 24px;
  font-weight: 500;
  display: flex;
  width: 336px;
  height: 44px;
  flex-shrink: 0;
}
.container,
.text {
  align-self: stretch;
}
.text {
  height: 44px;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
}
.container {
  border-radius: var(--br-md);
  background-color: var(--text-white);
  padding: var(--padding-lg);
}
.border,
.container,
.content,
.tablepage {
  flex-direction: column;
}
.border {
  align-self: stretch;
  flex: 1;
  border-radius: var(--br-md);
  background-color: var(--color-gray-200);
  overflow: hidden;
  padding: var(--padding-sm);
  align-items: flex-start;
  justify-content: flex-start;
}
.content,
.tablepage {
  align-items: center;
  justify-content: center;
}
.content {
  align-self: stretch;
  flex: 1;
  padding: 40px;
}
.tablepage {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-image: url(../../public/tablepage@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  text-align: center;
  font-size: var(--font-size-xl);
  color: var(--color-black);
  font-family: var(--font-roboto);
}
@media screen and (max-width: 1200px) {
  .tablepage {
    width: 100%;
    height: 100vh;
  }
}
@media screen and (max-width: 428px) {
  .logOut {
    display: none;
  }
  .small {
    display: flex;
  }
}
